import React, { Component } from "react";
import { Route, Switch, HashRouter } from "react-router-dom";
import Home from "../pages/home/HomeComponent";
import Splash from "../pages/splash/Splash";
import Education from "../pages/education/EducationComponent";
import Experience from "../pages/experience/Experience";
// import Opensource from "../pages/opensource/Opensource";
import Contact from "../pages/contact/ContactComponent";
import Projects from "../pages/projects/Projects";

import Printer from "../pages/projects/printer";
import Solenoid from "../pages/projects/solenoid";
import Mstirrer from "../pages/projects/stirrer";
import Scover from "../pages/projects/cover";
import PCswitches from "../pages/projects/switches";
import Compartiment from "../pages/projects/compartiment";




import { settings } from "../portfolio.js";
import Error404 from "../pages/errors/error404/Error";

export default class Main extends Component {
  render() {
    if (settings.isSplash) {
      return (
        <div>
          <HashRouter basename="/">
            <Switch>
              <Route
                path="/"
                exact
                render={(props) => (
                  <Splash {...props} theme={this.props.theme} />
                )}
              />
              <Route
                path="/home"
                render={(props) => <Home {...props} theme={this.props.theme} />}
              />
              <Route
                path="/experience"
                exact
                render={(props) => (
                  <Experience {...props} theme={this.props.theme} />
                )}
              />
              <Route
                path="/education"
                render={(props) => (
                  <Education {...props} theme={this.props.theme} />
                )}
              />
              {/*<Route
                path="/opensource"
                render={(props) => (
                  <Opensource {...props} theme={this.props.theme} />
                )}
              />*/}



              <Route
                path="/printer"
                render={(props) => (
                  <Printer {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/solenoid"
                render={(props) => (
                  <Solenoid {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/stirrer"
                render={(props) => (
                  <Mstirrer {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/cover"
                render={(props) => (
                  <Scover {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/switches"
                render={(props) => (
                  <PCswitches {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/compartiment"
                render={(props) => (
                  <Compartiment {...props} theme={this.props.theme} />
                )}
              />





              <Route
                path="/contact"
                render={(props) => (
                  <Contact {...props} theme={this.props.theme} />
                )}
              />
              <Route
                path="/splash"
                render={(props) => (
                  <Splash {...props} theme={this.props.theme} />
                )}
              />
              <Route
                path="/projects"
                render={(props) => (
                  <Projects {...props} theme={this.props.theme} />
                )}
              />
              <Route
                path="*"
                render={(props) => (
                  <Error404 {...props} theme={this.props.theme} />
                )}
              />
            </Switch>
          </HashRouter>
        </div>
      );
    } else {
      return (
        <div>
          <HashRouter basename="/">
            <Switch>
              <Route
                path="/"
                exact
                render={(props) => <Home {...props} theme={this.props.theme} />}
              />
              <Route
                path="/home"
                render={(props) => <Home {...props} theme={this.props.theme} />}
              />
              <Route
                path="/experience"
                exact
                render={(props) => (
                  <Experience {...props} theme={this.props.theme} />
                )}
              />
              <Route
                path="/education"
                render={(props) => (
                  <Education {...props} theme={this.props.theme} />
                )}
              />
              {/* <Route
                path="/opensource"
                render={(props) => (
                  <Opensource {...props} theme={this.props.theme} />
                )}
              />*/}


              { <Route
                path="/printer"
                render={(props) => (
                  <Printer {...props} theme={this.props.theme} />
                )}
              />}  
                            <Route
                path="/solenoid"
                render={(props) => (
                  <Solenoid {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/stirrer"
                render={(props) => (
                  <Mstirrer {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/cover"
                render={(props) => (
                  <Scover {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/switches"
                render={(props) => (
                  <PCswitches {...props} theme={this.props.theme} />
                )}
              />
                            <Route
                path="/compartiment"
                render={(props) => (
                  <Compartiment {...props} theme={this.props.theme} />
                )}
              />



              <Route
                path="/contact"
                render={(props) => (
                  <Contact {...props} theme={this.props.theme} />
                )}
              />
              {/* <Route
							path="/splash"
							render={(props) => (
								<Splash
									{...props}
									theme={this.props.theme}
								/>
							)}
						/> */}
              <Route
                path="/projects"
                render={(props) => (
                  <Projects {...props} theme={this.props.theme} />
                )}
              />
            </Switch>
          </HashRouter>
        </div>
      );
    }
  }
}
