import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import imcompartiment from "./compartiment.png";
import TopButton from "../../components/topButton/TopButton";
import "./compartiment.css";

class compartiment extends Component {
  render() {
    return (
      <div className="compartiment-main">
        <Header theme={this.props.theme} />
        <div className="image-container">
          <img src={imcompartiment} alt="FullImage" className="full-page-image" />
        </div>
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default compartiment;
