import React, { useState, useEffect } from 'react';
import "./Greeting.css";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import { greeting } from "../../portfolio";
import { Fade } from "react-reveal";
import FeelingProud from "./imGreating";
import FeelingProud2 from "./imGreeting_wave";
import FeelingProud3 from "./imGreating";

export default function Greeting(props) {
  const theme = props.theme;
  const [currentImage, setCurrentImage] = useState(1);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentImage((prevImage) => {
        if (prevImage === 1) {
          return 2; // After 1.5 seconds, switch to the second image
        } else {
          return 3; // After 1.5 seconds, switch to the third image (which remains unchanged)
        }
      });
    }, 1500);

    return () => {
      clearInterval(timer);
    };
  }, []);

  // Function to render the appropriate image component based on the currentImage state
  const renderImageComponent = () => {
    switch (currentImage) {
      case 1:
        return <FeelingProud theme={theme} />;
      case 2:
        return <FeelingProud2 theme={theme} />;
      case 3:
        return <FeelingProud3 theme={theme} />;
      default:
        return null;
    }
  };

  useEffect(() => {
    const scrollByAmount = 100;

    const scrollDown = () => {
      window.scrollBy({
        top: scrollByAmount,
        behavior: "smooth",
      });
    };

    const timeoutId = setTimeout(scrollDown, 20000);

    return () => clearTimeout(timeoutId);
  }, []);

  return (
    <Fade bottom duration={2000} distance="40px">
      <div className="greet-main" id="greeting">
        <div className="greeting-main">
          <div className="greeting-text-div">
            <div>
              <h1 className="greeting-text" style={{ color: theme.text }}>
                {greeting.title}
              </h1>
              {greeting.nickname && 
                <h2 className="greeting-nickname" style={{ color: theme.imageHighlight }}>
                   {greeting.nickname} 
                </h2>
              }	  
              <p
                className="greeting-text-p subTitle"
                style={{ color: theme.secondaryText }}
              >
                {greeting.subTitle}
              </p>
              <p
                className="greeting-text-p subTitle2"
                style={{ color: theme.secondaryText }}
              >
                {greeting.subTitle2}
              </p>
              <SocialMedia theme={theme} />
              <div className="portfolio-repo-btn-div">
                <Button
                  text="See My Resume"
                  newTab={true}
                  href={greeting.resumeLink}
                  theme={theme}
                  className="portfolio-repo-btn"
                />
              </div>
            </div>
          </div>
          <div className="greeting-image-div">
            {renderImageComponent()}
          </div>
        </div>
      </div>
    </Fade>
  );
}